.ui.form.account-form .input-right .mono {
  display: block;
  margin-top: 0.75em;
}

.ui.form.account-form .input-right .debt-input {
  width: 30%;
}

.ui.form.account-form .input-right .debt-input input[type="number"] {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-right: 0;
}

.ui.form.account-form .input-right .value-input-small input[type="number"] {
  width: calc(100% - 70.19px);
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

